.login{
    display: grid;
    place-items: center;
    background-color: black;
    height: 100vh;
}

.login > img{
    width: 100%;

}

.login > a{
    padding: 20px;
    background-color: #1db945;
    border-radius: 99px;
    font-weight: 800;
    color: white;
    text-decoration: none;

}