.body{
    margin-left: 230px;
    flex: 1;
    height:800vh;
    background: linear-gradient( rgb(91, 87,115), rgba(0, 0, 0, 1));
    color:white;
    padding:30px;
}

.body__info{
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body__info > img{
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 45px 60px rgb(0, 0,0,0.5);
}

.body__text{
    flex: 1;
}
.body__text > h2{
    font-size: 48px;
    margin-bottom: 10px;
}

.body__text > p{
    font-size: 14px;
}

.body__songs{
    margin: 20px -30px;
}

.body__icons{
    display: flex;
    align-items: center;
}

.body__icons > .MuiSvgIcon-root{
  margin-right: 20px;
}

.body__shuffle{
    font-size: 80px !important;
    margin: 20px 50px;

}

.body__shuffle:hover{
    transform: scale(1.08);
    transition: transform 0.1s ease-in;
} 